.super-rare{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-top: 6rem;
    margin-bottom: 3rem;
    .title-container{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        .title{
            color: var(--accent-color1);
            font-size: 2.4rem;
        }
        .des{
            color: var(--accent-color3);
            a{
                font-weight: bold;
                color: var(--accent-color3);
            }
        }
    }
    .cards{
        display: flex;
        justify-content: space-evenly;
        gap: 1rem;
    }
}

@media screen and (min-width: 280px) and (max-width: 1080px) {
    .super-rare{
        padding: 1rem 2rem;
        width: 100vw;
        .cards{
            overflow: auto;
            justify-content: flex-start;
            gap: 2rem;
        }
    }
}