.clients{
    padding: 2rem 0; 
    .container{
        .client-container{
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            text-align: center;
        }
    }
}

@media screen and (min-width: 280px) and (max-width: 1080px) {
    .clients{
        padding: 1rem 2rem;
        .container{
            .client-container{
                grid-template-columns: repeat(2, 1fr);
                text-align: left;
                gap: 1.5rem;
                .client{
                    img{
                        height: 1.8rem;
                    }
                }
            }
        }
    }
}