.free{
    position: relative;
    margin: 5rem 0;
    .container{
        display: flex;
        position: relative;
        background-color: var(--orange);
        height: 25rem;
        border-radius: 1rem;
        overflow: hidden;
        z-index: 1;
        .background{
            z-index: -1;
            .ellipse{
                position: absolute;
                height: 100%;
                width: 28rem;
                filter: blur(100px);
                border-radius: 100%;
            }
            .pink{
                top: -10%;
                left: -15%;
                background-color: var(--pink);
            }
            .green{
                top: -10%;
                right: -15%;
                background-color: var(--green);
            }
        }
        .content{
            display: flex;
            flex-direction: column;
            gap: 1.5rem;
            font-size: 1.3rem;
            padding-top: 3rem;
            padding-left: 5rem;
            z-index: 1;
            font-size: 1.3rem;
        }
    }
    .cards{
        display: flex;
        position: absolute;
        top: -2rem;
        right: 40%;
        .cards1{
            top: 0;
            position: absolute;
            z-index: 3;
            font-size: 0.6rem;
            transform: rotate(-10deg);
            .card-image{
               img{
                height: 10rem;
                width: 12rem;
               }
            }
        }
        .cards2{
            top: 0;
            left: 10rem;
            position: absolute;
            z-index: 2;
            font-size: 0.7rem;
            transform: rotate(5deg);
            .card-image{
               img{
                height: 10rem;
               }
            }
        }
    }
    @media screen and (min-width: 280px) and (max-width: 1080px) {
      
        .container{
            padding: 1rem 2rem;
            height: 40vh;
            .background{
                .pink{
                    left: -70%;
                }
                .green{
                    left: -70%;
                }
            }
            .content{
                padding: 0;
                font-size: 16px;
                gap: 1rem;
            }
        }
        .cards{
            .cards1, .cards2{
                zoom: 0.5;
            }
        }
        
    }
}