.releases{
    margin: 5rem 0;
    display: flex;
    flex-direction: column;
    gap: 5rem;
    .release{
        display: grid;
        grid-template-columns: 1fr 1fr;
        border-radius: 0.5rem;
        .content{
            padding: 5rem;
            display: flex;
            flex-direction: column;
            gap: 2rem;
            .title{
                font-size: 2rem;
            }
            .des{
                font-size: 1.3rem;
                a{
                    color: black;
                    font-weight: bold;
                }
            }
            .link{
                font-weight: bold;
                display: flex;
                gap: 1rem;
                text-decoration: none;
                svg{
                    font-size: 1.2rem;
                }
            }
        }
        .image{
            position: relative;
            overflow: hidden;
            z-index: 1;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            text-align: center;
            img{
                height: 24rem;
            }
            .ellipse{
                position: absolute;
                height: 20rem;
                width: 20rem;
                filter: blur(100px);
                bottom: -40%;
                z-index: -1;
                right: 0;
            }
            .pink{
                background-color: var(--pink);
            }
        }
        .card-container{
            position: relative;
            overflow: hidden;
            z-index: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            .ellipse{
                position: absolute;
                height: 20rem;
                width: 20rem;
                filter: blur(100px);
                bottom: -45%;
                z-index: -1;
                left: 0;
            }
            .orange{
                background-color: var(--orange);
            }
        }
    }
    .orange{
        background-color: var(--orange);
    }
    .green{
        background-color: var(--green);
    }
}

@media screen and (min-width: 280px) and (max-width: 1080px) {
    .releases{
       .release{
           grid-template-columns: 1fr;
           padding: 1rem 2rem;
           border-radius: 0;
           gap: 2rem;
           &:nth-of-type(1){
               .image{
                   grid-row: 1;
               }
           }
           .content{
               padding: 0;
           }
           .image{
               zoom: 0.7;
           }
           .card-container{
               zoom: 0.7;
           }
           .ellipse{
               display: none;
           }
       }
    }
}