.signup {
    padding-top: 4rem;
    border-top: 0.1rem solid white;
    border-bottom: 0.1rem solid white;
    overflow-y: hidden;
    .container {
      display: grid;
      grid-template-columns: 1fr 1fr;
    //   font-size: 32px;
      .content {
        margin-top: 3rem;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        align-items: flex-start;
        .sub-title {
          color: var(--pink);
          text-transform: uppercase;
          letter-spacing: 0.1rem;
          font-size: 20px;
        }
        .title {
          color: var(--accent-color1);
          font-size: 45px;
        }
        .description {
          color: var(--accent-color3);
          font-size: 18px;
        }
        button {
          color: var(--accent-color1);
          background-color: var(--background);
          font-weight: bold;
          padding: 1rem 3rem;
          border-radius: 2rem;
          box-shadow: 0 0 6px 0 rgba(157, 96, 212, 0.5);
          border: solid 3px transparent;
          background-image: linear-gradient(
              rgba(255, 255, 255, 0),
              rgba(255, 255, 255, 0)
            ),
            linear-gradient(101deg, var(--pink), var(--orange));
          background-origin: border-box;
          background-clip: content-box, border-box;
          box-shadow: 2px 1000px 1px var(--background) inset;
          transition: 0.5s ease-in-out;
          cursor: pointer;
          &:hover {
            box-shadow: none;
          }
        }
      }
      .image-container {
        text-align: center;
        position: relative;
        z-index: 10;
      
        .ellipse-container {
          .ellipse {
            z-index: -1;
            position: absolute;
            height: 15rem;
            width: 15rem;
            filter: blur(100px);
          }
          .pink {
            top: 40%;
            right: 40%;
            background-color: var(--pink);
          }
          .orange {
            bottom: 40%;
            left: 40%;
            background-color: var(--orange);
          }
        }
      }
    }
  }
  
  @media screen and (min-width: 280px) and (max-width: 1080px) {
    .signup{
      padding: 1rem 3rem;
      overflow: hidden;
      // margin-top: 1rem;
      .container{
        grid-template-columns: 1fr;
        .content{
          margin-bottom: 2rem;
          .sub-title{
            font-size: 17px;
          }
          .title{
            font-size: 40px;
          }
          .description{
            font-size: 17px;
          }
        }
        .image-container{
          margin: 2rem 0;
          grid-row: 1;
          .image{
            img{
              height: 25rem;
            }
          }
        }
      }
    }
  }


 